import {mapState} from 'vuex';
import { getComments } from '@/api/article.js';

export default {
    name: 'comments',
    props: ['article'],
    data () {
        return {
            comments: []
        };
    },
    computed: {
        ...mapState(['user'])
    },
    created () {
        getComments(this.article.slug).then(({data}) => {
            this.comments = data.comments;
        });
    }
}
